#navus{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: black;
    padding: 20px 80px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.06);
    font-size: 1.3rem;
    font-weight: 600;
    color: aliceblue;
}

#navi{
    display: flex;
    align-items: center;
    justify-content: center;
}
#navi li{
    list-style: none;
    padding: 0 20px;
    position: relative;
}
#navi li a{
    text-decoration: none;
    font-size: 1.3rem;
    font-weight: 600;
    color: aliceblue;
    transition: 0.3s ease-in-out;
}
#navi li a:hover{
    color: yellow;

}